<template>
    <div>
        <Nav/>
           <div class="container pay_by" >
                <div v-if="current.pay_by=='wechat'" class="w-pay">
                    <p></p>
                    <img :src="qrcode" alt="">
                </div>
                <div v-else>
                    ...
                    <!-- <a :href="qrcode"></a> -->
                </div>
                <div>
                    <button @click="verify"></button>
                </div>
         </div>
    </div>
</template>
<script>
    import Nav from '../components/Nav';
    import api from '../lib/api';
    import {url} from '../lib/url';

    export default {
        components:{Nav},
        data(){
            return{
                current:{},
                qrcode:'',
            }
        },
        mounted() {
            this.find(this.$route.params);
        },
        methods:{
            find(id){
                api('order/first',{id})
                    .then(r=>{
                        let row = this.current = r.data;
                        console.log('this.current',this.current);
                        
                        if(!row){
                            alert('')
                            this.go_me_order();
                        }

                        if(row._paid){
                            alert('')
                            this.go_me_order();
                        }

                        this.pay(row.id,row.pay_by,row.sum)
                    })
            },
            go_me_order(){
                this.$router.push('/me/order')
            },
            pay(id,pay_by,fee){
                if(fee > .2)
                    fee = .2;
                return api('order/payment/url',{
                    id:id,
                    pay_by:pay_by,
                    fee:fee,
                    return_url:url('/#/'),
                }).then(r=>{
                    if(r.data.url){
                        location.href = r.data.url; //urlurl
                    }else{
                        this.qrcode = r.data.qrcode //
                    }
                })
            },
            verify(){
                api('order/find',{id:this.current.id})
                    .then(r=>{
                        if(r.data._paid){
                            alert('');
                            this.$router.push('/search');
                        }
                        else
                            alert('');
                    });
            }
        },
    }
</script>
<style scoped>
.pay_by >*{
    text-align: center;
}
.pay_by img{
    display: inline-block;
}
</style>
